"use client"

import { useAuth } from "@/context/auth-context"
import { useRouter } from "next/navigation"
import { useEffect } from "react"
import { MdDashboard, MdLocalGasStation, MdWork, MdPayments, MdNotifications, MdPerson } from "react-icons/md"
import Link from "next/link"

export default function AttendantLayout({ children }: { children: React.ReactNode }) {
  const { user, isAuthenticated, logout } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated || user?.role !== "ATTENDANT") {
      router.push("/login")
    }
  }, [isAuthenticated, user, router])

  return (
    <div className="min-h-screen flex bg-gray-50 text-gray-900">
      {/* Sidebar */}
      <aside className="w-64 bg-[#1a3a3a] text-white flex flex-col p-4">
        <h1 className="text-2xl font-bold text-center mb-6">â›½ Attendant</h1>
        <nav className="flex flex-col space-y-3">
          <NavLink href="/attendant/dashboard" icon={<MdDashboard />}>Dashboard</NavLink>
          <NavLink href="/attendant/sales" icon={<MdLocalGasStation />}>Fuel Sales</NavLink>
          <NavLink href="/attendant/shifts" icon={<MdWork />}>Shifts</NavLink>
          <NavLink href="/attendant/payments" icon={<MdPayments />}>Payments</NavLink>
          <NavLink href="/attendant/alerts" icon={<MdNotifications />}>Alerts</NavLink>
          <NavLink href="/attendant/profile" icon={<MdPerson />}>Profile</NavLink>
        </nav>
        <button
          onClick={logout}
          className="mt-auto bg-red-600 hover:bg-red-700 text-white font-semibold py-2 rounded-lg"
        >
          Logout
        </button>
      </aside>

      {/* Main content */}
      <main className="flex-1 flex flex-col">
        {/* Top Bar */}
        <header className="bg-white shadow-sm p-4 flex justify-between items-center">
          <h2 className="text-lg font-semibold">Welcome, {user?.name}</h2>
          <span className="text-sm text-gray-600">Role: {user?.role}</span>
        </header>
        <div className="p-6">{children}</div>
      </main>
    </div>
  )
}

function NavLink({ href, icon, children }: { href: string; icon: React.ReactNode; children: React.ReactNode }) {
  return (
    <Link
      href={href}
      className="flex items-center gap-3 p-2 rounded-lg hover:bg-teal-600 focus:ring-2 focus:ring-yellow-400"
    >
      <span aria-hidden="true">{icon}</span>
      <span className="font-medium">{children}</span>
    </Link>
  )
}
